import React,{useState} from 'react';
import ReactDOM from 'react-dom';
import './App.css';

function App()  {
    const [Hori,updateHori] = useState(10);
    const [Veri,updateVeri] = useState(10);
    const [Blur,updateBlur] = useState(10);
    const [Color,updateColor] = useState('black');
    const [Inset,updateInset] = useState(false);

    
    return (
        <div>
            <h1 className="center" style={{fontFamily:"Grand Hotel"}}>Shadow Generator</h1>
            <div className="App">
                <div className="controls range-field">
                    <label>Shadow Color</label>
                    <input type="color" value={Color} onChange={(e)=>updateColor(e.target.value)} />
                    <br/>
                    <label>Horizontal</label>
                    <input type="range" min="-100" max="100" value={Hori} onChange={(e)=>updateHori(e.target.value)} />
                    
                    <label>Vertical</label>
                    <input type="range" min="-100" max="100" value={Veri} onChange={(e)=>updateVeri(e.target.value)} />
                    
                    <label>Blur</label>
                    <input type="range" min="-100" max="100" value={Blur} onChange={(e)=>updateBlur(e.target.value)} />

                    <div className="switch">
                        <label>
                            Outline
                            <input type="checkbox" value={Inset} onChange={(e)=>updateInset(!Inset)} />
                            <span className="lever"></span>
                            Inset
                        </label>
                    </div>
                                        
                </div>
                <div>
                    <div className="box" style={{boxShadow: `${Inset? "inset": ""} ${Hori}px ${Veri}px ${Blur}px ${Color}`}}>
                        <p className="innerbox">
                            -webkit-box-shadow: {Inset? "inset": ""} {Hori}px {Veri}px {Blur}px {Color}
                            <br />
                            -moz-box-shadow: {Inset? "inset": ""} {Hori}px {Veri}px {Blur}px {Color}
                            <br />
                            box-shadow: {Inset? "inset": ""} {Hori}px {Veri}px {Blur}px {Color}
                        </p>
                    </div>    
                </div>
            </div>
        </div>
    )
    
}


ReactDOM.render(<App />,document.getElementById("root"));